<template>
  <el-form ref="passFrom" :model="passFrom" label-width="100px" :rules="rules">
    <el-form-item label="旧密码" prop="oldpassword">
      <el-input type="password" v-model="passFrom.oldpassword"></el-input>
    </el-form-item>
    <el-form-item label="新密码" prop="newpassword">
      <el-input type="password" v-model="passFrom.newpassword"></el-input>
    </el-form-item>
    <el-form-item label="确认密码" prop="newpassword2">
      <el-input type="password" v-model="passFrom.newpassword2"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit">立即修改</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "UpdataPass",
  data() {
    return {
      passFrom: {
        oldpassword: "",
        newpassword: "",
        newpassword2: "",
      },
      rules: {
        oldpassword: [
          {
            validator(rule, value, callback) {
              //验证字符长度
              if (value.length < 6 || value.length > 18) {
                callback(new Error("请输入6-18位字符"));
              } else {
                //字符验证
                if (/^[a-zA-Z]\w{5,17}$/.test(value)) {
                  callback();
                } else {
                  callback(new Error("以字母开头,只能包含字符、数字和下划线"));
                  callback(); //验证失败
                }
              }
            },
          },
        ],
        newpassword: [
          {
            validator(rule, value, callback) {
              //验证字符长度
              if (value.length < 6 || value.length > 18) {
                callback(new Error("请输入6-18位字符"));
              } else {
                //字符验证
                if (/^[a-zA-Z]\w{5,17}$/.test(value)) {
                  callback();
                } else {
                  callback(new Error("以字母开头,只能包含字符、数字和下划线"));
                  callback(); //验证失败
                }
              }
            },
          },
        ],
        newpassword2: [
          {
            validator: (rule, value, callback) => {
              if (this.passFrom.newpassword === value) {
                callback(); //密码一致
              } else {
                callback(new Error("不一致 "));
              }
            },
          },
        ],
      },
    };
  },
  methods: {
    ...mapMutations(["cleardatauser"]),
    onSubmit() {
      this.$refs.passFrom.validate(async (v) => {
        if (v) {
          let { data } = await this.$axios({
            method: "POST",
            url: "/person/pass",
            data: {
              oldpassword: this.passFrom.oldpassword,
              newpassword: this.passFrom.newpassword,
            },
          });
          //修改失败
          if (data.code) return this.$message.error(data.message);
          //修改成功
          this.$message.success(data.message);
          this.cleardatauser(); //清空vuex中的数据
          this.$router.push("/Home");
        } else {
          return false;
        }
      });
    },
  },
};
</script>

